.buyContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.contacts {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  row-gap: 30px;
  justify-content: center;
}

.goBack {
  position: fixed;
  top: 70px;
  left: 5px;
  display: inline-flex;
  text-decoration: none;
  border: 1px solid #e6eaed;
  border-radius: 0 15px 15px 0;
  width: 70px;
  height: 20px;
  margin-block-end: 0;
  margin-block-start: 0;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 14px;
  transition: all 0.003s;
  background-color: #b5c2e0;
}

.goback:hover {
  background-color: transparent;
  color: #495464;
}

