.mapContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.addressSearchContainer {
  max-width: 350px;
  margin: 120px 0 25px;
  z-index: 10;
}

@media screen and (min-width: 1440px) {
  .buyContainer {
    gap: 30px;
  }
}
