.homeList {
  font-weight: 400;
  color: black;
  list-style: none;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  margin-inline-start: 0;
  padding-inline-start: 0;
  margin-top: 60px;
}

.itemHome {
  border: 1.5px solid #e6eaed;
  font-size: 8px;
  width: 300px;
  height: auto;
  border-radius: 10px;
  background-color: white;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  cursor: pointer;
  color: #4d4c4c;
  box-shadow: -5px 5px 5px 3px rgba(177, 185, 204, 0.12),
    10px 5px 5px 3px rgba(177, 185, 204, 0.14),
    -5px 5px 5px 3px rgba(177, 185, 204, 0.12),
    -5px 5px 5px 3px rgba(177, 185, 204, 0.12);
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0.4 0.4, 0.4);
}

.itemHome:hover {
  scale: 1.03;
  transition: all 0.3s;
}

.everyItem {
  display: flex;
  flex-wrap: wrap;
}

/* .everyItem > button, */

.everyItemImg {
  width: 140px;
  height: auto;
}

.everyItem > img {
  border-radius: 10px 0 0 10px;
}

.about {
  display: block;
  padding: 10px 0 10px 10px;
}

.price {
  font-size: 10px;
  font-weight: bold;
  margin-block-end: 0;
  margin-block-start: 0;
  padding-bottom: 10px;
}

.address {
  margin-block-end: 0;
  margin-block-start: 0;
}

.aboutDetails {
  display: flex;
  flex-direction: row;
  gap: 3px;
}

.aboutDetails > p {
  margin-block-start: 8px;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-bottom: 10px;
}

.icon {
  line-height: 0;
  padding: 0 2px 0 0;
  width: 10px;
  height: 10px;
}

.toHomeElement {
  display: flex;
  font-family: 'Montserrat', sans-serif;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  border-radius: 5px;
  font-size: 12px;
  color: #4d4c4c;
  width: 120px;
  height: 36px;
  border: 1px solid #4d4c4c;
  background-color: white;
  cursor: pointer;
}

.toHomeElement:hover,
.toHomeElement:focus {
  background-color: #4d4c4c;
  color: white;
  transition: all 0.3s;
}

@media screen and (min-width: 1440px) {
  .homeList {
    font-weight: 400;
    color: black;
    list-style: none;
    display: flex;
    flex-direction: column;
    row-gap: 30px;
    margin-inline-start: 0;
    padding-inline-start: 0;
    margin-top: 120px;
  }
  .itemHome {
    border: 1.5px solid #e6eaed;
    font-size: 16px;
    width: 700px;
    height: auto;
    max-height: 250px;
    border-radius: 20px;
    background-color: white;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    cursor: pointer;
    color: #4d4c4c;
    box-shadow: -5px 5px 5px 3px rgba(177, 185, 204, 0.12),
      10px 5px 5px 3px rgba(177, 185, 204, 0.14),
      -5px 5px 5px 3px rgba(177, 185, 204, 0.12),
      -5px 5px 5px 3px rgba(177, 185, 204, 0.12);
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0.4 0.4, 0.4);
  }
  .everyItem {
    display: flex;
    flex-wrap: wrap;
  }
  /* .everyItem > button, */
  .everyItemImg {
    width: 320px;
    max-height: 250px;
  }
  .everyItem > img {
    border-radius: 20px 0 0 20px;
  }
  .about {
    display: block;
    padding: 20px;
  }
  .price {
    font-size: 18px;
    font-weight: bold;
    margin-block-end: 0;
    margin-block-start: 0;
    padding-bottom: 15px;
  }
  .address {
    margin-block-end: 0;
    margin-block-start: 0;
  }
  .aboutDetails {
    display: flex;
    flex-direction: row;
    gap: 20px;
  }
  .aboutDetails > p {
    margin-block-start: 15px;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
    padding-bottom: 60px;
  }
  .icon {
    line-height: 0;
    padding: 0 2px 0 0;
    width: 20px;
    height: 20px;
  }
  .toHomeElement {
    display: flex;
    font-family: 'Montserrat', sans-serif;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    border-radius: 5px;
    font-size: 16px;
    color: #4d4c4c;
    width: 120px;
    height: 40px;
    border: 1px solid #4d4c4c;
    background-color: white;
    cursor: pointer;
  }
}

