.container {
    width: 90vw;
    aspect-ratio: 1 / 1;
}

.itemHome {
    margin-block-end: 0;
    margin-block-start: 0;
    padding: 0;
    border: 1.5px solid #e6eaed;
    font-size: 10px;
    width: 320px;
    height: auto;
    background-color: white;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    cursor: pointer;
    color: #4d4c4c;
    box-shadow: -5px 5px 5px 3px rgba(177, 185, 204, 0.12), 10px 5px 5px 3px rgba(177, 185, 204, 0.14), -5px 5px 5px 3px rgba(177, 185, 204, 0.12), -5px 5px 5px 3px rgba(177, 185, 204, 0.12);
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0.4 0.4, 0.4);
    border-radius: 5px;
}

.everyItem {
    margin-block-end: 0;
    margin-block-start: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    border-radius: 5px;
}

.everyItemImg {
    border-radius: 5px 0 0 5px;
}

.about {
    display: block;
    padding: 4px 0 0 10px;
    border-radius: 5px;
}

.price {
    font-size: 12px;
    font-weight: bold;
    margin-block-end: 0;
    margin-block-start: 0;
    padding-bottom: 4px;
}

.address {
    margin-block-end: 0;
    margin-block-start: 0;
}

.aboutDetails {
    display: flex;
    flex-direction: row;
    gap: 4px;
}

.aboutDetails>p {
    margin-block-start: 5px;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
    padding-bottom: 5px;
}

.icon {
    line-height: 0;
    padding: 0 0px 0 0;
    font-size: 8px;
}

.toHomeElement {
    display: flex;
    font-family: 'Montserrat', sans-serif;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    border-radius: 5px;
    font-size: 12px;
    color: #4d4c4c;
    width: 120px;
    height: 20px;
    border: 1px solid #4d4c4c;
    background-color: white;
    cursor: pointer;
    font-weight: bold;
}

@media screen and (min-width: 1440px) {
    .container {
        width: 40vw;
    }
}